import React from 'react';
import { insertMailingList } from './API';
import { isValidEmail } from './email';

interface MailingListState {
    email: string;
    validEmail: ValidityState;
}

enum ValidityState {
    Empty = "EMPTY",
    Invalid = "INVALID",
    Valid = "VALID",
    Error = "ERROR",
    Added = "ADDED",
}

// eslint-disable-next-line @typescript-eslint/no-empty-interface
interface IProps { }

export default class MailingList extends React.Component<IProps, MailingListState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            email: "",
            validEmail: ValidityState.Empty,
        }
    }

    handleOnChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
        const email = event.target.value;
        if (email === "") {
            this.setState({ validEmail: ValidityState.Empty, email: email })
            return
        }

        if (!isValidEmail(email)) {
            this.setState({ validEmail: ValidityState.Invalid, email: email })
            return
        }
        this.setState({ validEmail: ValidityState.Valid, email: email })
    }

    async addToList(): Promise<void> {
        try {
            await insertMailingList(this.state.email);
            this.setState({ validEmail: ValidityState.Added })
        } catch (error) {
            this.setState({ validEmail: ValidityState.Error })
        }
    }

    render(): JSX.Element {
        let className = "";
        let errorMessage: string | null = null;
        let buttonColor = "dark";

        switch (this.state.validEmail) {
            case ValidityState.Empty:
                break;
            case ValidityState.Invalid:
                className = "is-invalid";
                errorMessage = "Please enter a valid email address";
                buttonColor = "danger";
                break;
            case ValidityState.Valid:
                className = "is-valid";
                buttonColor = "success";
                break;
            case ValidityState.Error:
                return <h3 style={{ fontFamily: "Permanent Marker", color: "red" }}>Error, please try again later</h3>
            case ValidityState.Added:
                return <h3 style={{ fontFamily: "Permanent Marker", color: "green" }}>Email added, thank you!</h3>
        }

        return (
            <div>
                <div className="input-group mb-3">
                    <input type="text"
                        onBlur={this.handleOnChange}
                        onChange={this.handleOnChange}
                        className={"form-control " + className}
                        placeholder="Your email here..."
                        aria-describedby="button-mailingList"
                    />
                    <button
                        className={"btn btn-" + buttonColor}
                        style={{ fontFamily: "Permanent Marker" }}
                        type="button"
                        id="button-mailingList"
                        disabled={this.state.validEmail === ValidityState.Valid ? false : true}
                        onClick={async () => { await this.addToList() }}>Join</button>
                </div>

                <div className="form-text" style={{ color: "#dc3545", fontFamily: "Permanent Marker" }}>
                    {errorMessage}
                </div>
            </div >
        );
    }
}