import { apiLocation } from "./envvars";

export interface FreeTimeSlot {
    // change time to date
    date: Date;
    products: Array<string>;
    boxes: number;
}

export const getFreeTimeslots = async function (): Promise<FreeTimeSlot[]> {
    const freeTimeslotsResp = await apiGetTimeslots()

    const freeDates: FreeTimeSlot[] = [];
    for (const freeTimeslot of freeTimeslotsResp.freeTimeslots) {
        const unixTime = Date.parse(freeTimeslot.date)
        if (isNaN(unixTime)) {
            throw new Error("received an invalid timeslot");
        }
        freeDates.push({
            date: new Date(unixTime),
            products: freeTimeslot.products,
            boxes: freeTimeslot.boxes,
        })
    }
    return freeDates
}

export interface FreeTimeSlotsResp {
    freeTimeslots: Array<{
        date: string;
        products: Array<string>;
        boxes: number;
    }>
}

const apiGetTimeslots = async function (): Promise<FreeTimeSlotsResp> {
    const response = await fetch(apiLocation() + "/timeslots", {
        method: "GET",
    });

    if (response.ok) {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
        const timestamps: FreeTimeSlotsResp = await response.json()
        return timestamps;
    }

    return Promise.reject(response.statusText);
}

export const insertMailingList = async function (email: string): Promise<void> {
    const requestHeaders: HeadersInit = new Headers();
    requestHeaders.set('Content-Type', 'application/json');

    const response = await fetch(apiLocation() + "/mailinglist", {
        method: "POST",
        body: JSON.stringify({
            email: email,
        }),
        headers: requestHeaders,
    });

    if (!response.ok) {
        return Promise.reject(response.statusText);
    }

    return;
}